<template>
	<div id="goods-search">
		<public-header title="商品搜索"></public-header>
		<div class="content">
			<div class='search-header van-hairline--bottom'>
				<div class="search-content">
					<van-icon name="search"/>
					<input v-model="keyword" type="text" class="search-input" placeholder="请输入商品名称">
				</div>
				<div class="search-btn" @click="onSearch(true)">搜索</div>
			</div>
			<div class="goods-search-warp">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onSearch(false)"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
				>
					<div class="goods-list">
						<div class="goods-item van-hairline--bottom" v-for="(goodsItem, index) in records.data" :key="index">
							<div class="goods-info flex-lr">
								<div class="goods-thumb">
									<img :src="goodsItem.thumb" alt="">
									<div class="hot" v-if="goodsItem.label">{{goodsItem.label}}</div>
									<div class="status" v-if="goodsItem.status != 1">已下架</div>
								</div>
								<div class="flex-tb-left info-inner">
									<div class="font-bold w-100 ellipsis font-15">{{goodsItem.title}}</div>
									<div class="c-gray flex font-13">
										<span>已售: {{goodsItem.sailed}}</span>
										<span class="sailed-tip" v-if="goodsItem.total == -1">不限库存</span>
										<span class="margin-10-l" v-else>库存: {{goodsItem.total}}</span>
									</div>
									<div class="flex font-13">
										<span class="c-info">
											外卖: <span class="font-bold">¥{{goodsItem.price}}</span>
										</span>
										<span class="c-danger margin-10-l">
											店内: <span class="font-bold">¥{{goodsItem.ts_price}}</span>
										</span>
									</div>
								</div>
							</div>
							<div class="goods-btn">
								<div class="btn-item" @click="onChangeStatus('total', goodsItem, index)" v-if="goodsItem.total != 0">售罄</div>
								<router-link tag='div' class="btn-item" :to="util.getUrl({path: 'pages/goods/post', query: {id: goodsItem.id}})">编辑</router-link>
								<div class="btn-item" @click="onChangeStatus('status', goodsItem, index)">{{goodsItem.status == 1 ? '下架' : '上架'}}</div>
							</div>
						</div>
					</div>
				</van-list>
			</div>
			<div class="no-data" v-if="records.empty">
				<img src="static/img/order_no_con.png" alt="">
				<p>暂无相关商品，敬请期待</p>
			</div>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				records: {
					page: 1,
					psize: 10,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				keyword: '',
			}
		},
		methods: {
			onLoad(){

			},
			onSearch(force) {
				var that = this;
				if(!that.keyword) {
					that.util.$toast('请输入商品名称', '', 1000);
					return;
				}
				getRecords({
					vue: that,
					force: force,
					recordsName: 'goods',
					url: 'manage/goods/index/search',
					data: {
						keyword: that.keyword
					},
					success: function(res) {

					}
				})
			},
			onChangeStatus(type, goods, index) {
				var that = this;
				var id = goods.id;
				if(type == 'status') {
					var success_status = goods.status == 1 ? 0 : 1;
					var params = {
						url: 'manage/goods/index/status',
						confirm: goods.status == 1 ? '确定下架此商品吗?' : '确定上架此商品吗?',
						data: {
							id: id,
							value: success_status
						},
						success: function(res) {
							that.util.$toast(res);
							that.records.data[index].status = success_status;
						}
					};
				} else if(type == 'total') {
					var params = {
						url: 'manage/goods/index/turncate',
						confirm: '确定设置已售罄吗?',
						data: {id: id},
						success: function(res) {
							that.util.$toast(res);
							that.records.data[index].total = 0;
						}
					};
				}
				that.util.jspost(params);
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#goods-search .search-header{
		background-color: #fff;
		width: 100%;
		height: 50px;
		box-sizing: border-box;
		padding:10px;
		display:flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	#goods-search .search-header .search-content{
		width: 90%;
		height: 100%;
		border-radius: 40px;
		position: relative;
	}
	#goods-search .search-header .search-content .van-icon{
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 30px;
		line-height: 28px;
		text-align: center;
		font-size: 14px;
	}
	#goods-search .search-header .search-content .search-input{
		width: 100%;
		height: 100%;
		line-height: 28px;
		border-radius: 40px;
		padding-left: 25px;
		box-sizing: border-box;
		background: #f2f2f2;
		font-size: 14px;
	}
	#goods-search .search-header .search-btn{
		font-size: 14px;
		color: #33aafc;
	}
	#goods-search .goods-list .goods-item{
		position: relative;
		padding: 10px 15px;
		background-color: #fff;
	}
	#goods-search .goods-list .goods-item .goods-info .goods-thumb{
		width: 60px;
		height: 60px;
		border-radius: 5px;
		overflow: hidden;
		position: relative;
	}
	#goods-search .goods-list .goods-item .goods-info .goods-thumb .hot{
		position: absolute;
		z-index: 10;
		top: 0;
		left: 0;
		height: 15px;
		line-height: 15px;
		background-color: #FF2D4B;
		color: #fff;
		padding: 0 5px;
		font-size: 11px;
		text-align: left;
	}
	#goods-search .goods-list .goods-item .goods-info .goods-thumb .status{
		position: absolute;
		z-index: 10;
		bottom: 0;
		left: 0;
		height: 15px;
		line-height: 15px;
		width: 100%;
		background-color: #999;
		color: #fff;
		font-size: 11px;
		text-align: center;
	}
	#goods-search .goods-list .goods-item .goods-info .info-inner{
		height: 60px;
		flex: 1;
		padding-left: 10px;
	}
	#goods-search .goods-list .goods-item .goods-info .info-inner .sailed-tip{
		padding: 1px 3px;
		color: #33aafc;
		border: 1px solid #33aafc;
		border-radius: 2px;
		font-size: 11px;
		margin-left: 10px;
	}
	#goods-search .goods-list .goods-item .goods-info .goods-thumb img{
		width: 100%;
		height: 100%;
	}
	#goods-search .goods-list .goods-item .goods-btn{
		margin-top: 10px;
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-end;
		justify-content: flex-end;
	}
	#goods-search .goods-list .goods-item .goods-btn .btn-item{
		font-size: 14px;
		padding: 5px 10px;
		color: #33aafc;
		border: 1px solid #33aafc;
		border-radius: 3px;
		margin-left: 10px;
	}
	#goods-search .goods-list .goods-item .goods-btn .btn-item:first-child{
		margin: 0;
	}
</style>


